import { File, Mail, Printer } from 'react-feather';
import { StyledButton, StyledWelcomeArea, StyledWelcomeLeft, StyledWelcomeRight } from './style';
import { useEffect, useState } from 'react';

import Breadcrumb from '../../../components/breadcrumb';
import ModalPrint from '../../../components/dashboard-one/modal-print';
import ModalReport from '../../../components/dashboard-one/modal-report';
import { Select } from '@doar/components';
import moment from 'moment';

const WelcomeArea = ({
    selectYear,
    setSelectYear,
    companyNm,
    topChartData,
}: {
    selectYear: string;
    setSelectYear: React.Dispatch<React.SetStateAction<string>>;
    companyNm: string;
    topChartData: any;
}) => {
    const [yearList, setYearList] = useState<string[]>([]);
    useEffect(() => {
        //  
        const thisYear = moment().format('YYYY');

        //  
        const lastYear = moment().subtract(1, 'years').format('YYYY');

        //  
        const twoYearsAgo = moment().subtract(2, 'years').format('YYYY');
        setYearList([thisYear, lastYear, twoYearsAgo]);
    }, []);
    const [showPrintModal, setShowPrintModal] = useState(false);
    const [showReportModal, setShowReportModal] = useState(false);
    const handlePrintModal = () => {
        setShowPrintModal(prev => !prev);
    };
    const handleReportModal = () => {
        setShowReportModal(prev => !prev);
    };
    return (
        <>
            <StyledWelcomeArea>
                <StyledWelcomeLeft>
                    <Breadcrumb
                        prev={[{ text: ' ', link: '/taxation' }]}
                        title=""
                        wcText={`${companyNm}  `}
                    />
                </StyledWelcomeLeft>
                <StyledWelcomeRight>
                    <div style={{ display: 'flex' }}>
                        <Select
                            id="createCategory"
                            name="createCategory"
                            value={selectYear}
                            onChange={e => {
                                setSelectYear(e.target.value);
                            }}
                            // showState={showState}
                            // state={errors.createCategory ? 'error' : 'success'}
                        >
                            <option value=""> </option>
                            {yearList.map(year => (
                                <option value={year} key={year}>
                                    {year}
                                </option>
                            ))}
                        </Select>
                        {/* </StyledGroup> */}
                        {/* <StyledButton size="sm" color="white" hasIcon ml="10px" onClick={handlePrintModal}>
                            <Printer />
                             
                        </StyledButton> */}
                    </div>
                    {/* <StyledGroup>
                        <StyledLabel htmlFor="createCategory">Category</StyledLabel> */}
                </StyledWelcomeRight>
            </StyledWelcomeArea>
            {/* <ModalPrint show={showPrintModal} onClose={handlePrintModal} topChartData={} /> */}
            <ModalReport show={showReportModal} onClose={handleReportModal} />
        </>
    );
};

export default WelcomeArea;
