import moment from 'moment';

interface TopChartProps {
    data?: any;
    selectYear?: any;
}
const TopChart = async ({ data, selectYear }: TopChartProps) => {
    const returnData = {
        section1: {
            amount: 0,
            percent: 0,
            time: '',
        },
        section2: {
            amount: 0,
            percent: 0,
            time: '',
        },
        section3: {
            amount: 0,
            percent: 0,
            time: '',
        },
        section4: {
            amount: 0,
            percent: 0,
            time: '',
        },
    };
    if (data?.data?.quarterlyRevenue) {
        console.log('----------------');

        console.log(selectYear);
        console.log(data?.data?.quarterlyRevenue);
        let lastYear = false;
        const keysArray = Object.keys(data?.data?.quarterlyRevenue);
        const keyIndex = keysArray.indexOf(selectYear.toString());

        if (keyIndex !== -1) {
            if (keyIndex === 0) {
                lastYear = true;
            }
            const selectSection12Data = data?.data?.quarterlyRevenue[`${(Number(selectYear) - 1).toString()}`];

            const listLastYear = Number(moment().format('YYYY')) - 2;
            const listLastYearItem = keysArray.indexOf(listLastYear.toString());
            if (Number(selectYear) > listLastYear && listLastYearItem) {
                const selectSection2Data = data?.data?.quarterlyRevenue[`${(Number(selectYear) - 2).toString()}`];
                let sumSelectSection12Data = 0;
                let sumSelectSection2Data = 0;
                await Promise.all(
                    selectSection2Data?.map((item: any) => {
                        sumSelectSection2Data = +Number(item.replace(/,/g, ''));
                    })
                );
                await Promise.all(
                    selectSection12Data?.map((item: any) => {
                        sumSelectSection12Data = +Number(item.replace(/,/g, ''));
                    })
                );
                const percentageIncrease =
                    ((sumSelectSection12Data - sumSelectSection2Data) / sumSelectSection2Data) * 100;
                const increasevalue = percentageIncrease.toFixed(2);

                returnData['section2'] = {
                    amount: sumSelectSection12Data,
                    percent: Number(increasevalue),
                    time: selectYear.toString(),
                };
                console.log({ section2: returnData.section2 });
            }

            if (selectSection12Data?.length > 1) {
                const percentageIncrease =
                    ((Number(selectSection12Data[selectSection12Data?.length - 1].replace(/,/g, '')) -
                        Number(selectSection12Data[selectSection12Data?.length - 2].replace(/,/g, ''))) /
                        Number(selectSection12Data[selectSection12Data?.length - 2].replace(/,/g, ''))) *
                    100;
                const increasevalue = percentageIncrease.toFixed(2);

                returnData['section1'] = {
                    amount: Number(selectSection12Data[selectSection12Data?.length - 1].replace(/,/g, '')),
                    percent: Number(increasevalue),
                    time: `${selectSection12Data.length}`,
                };
                console.log({ section1: returnData.section1 });
            }
        }
    }
    if (data?.data?.profitAndLoss) {
        console.log('----------------');

        console.log(selectYear);
        console.log(data?.data?.profitAndLoss);
        let lastYear = false;
        const keysArray = Object.keys(data?.data?.profitAndLoss);
        const keyIndex = keysArray.indexOf(selectYear.toString());

        if (keyIndex !== -1) {
            if (keyIndex === 0) {
                lastYear = true;
            }
            const selectSection12Data = data?.data?.profitAndLoss[`${(Number(selectYear) - 1).toString()}`];

            const listLastYear = Number(moment().format('YYYY')) - 2;
            const listLastYearItem = keysArray.indexOf(listLastYear.toString());
            if (Number(selectYear) > listLastYear && listLastYearItem) {
                const selectSection2Data = data?.data?.profitAndLoss[`${(Number(selectYear) - 2).toString()}`];
                let sumSelectSection12Data = 0;
                let sumSelectSection2Data = 0;
                await Promise.all(
                    selectSection2Data?.map((item: any) => {
                        sumSelectSection2Data = +Number(item.replace(/,/g, ''));
                    })
                );
                await Promise.all(
                    selectSection12Data?.map((item: any) => {
                        sumSelectSection12Data = +Number(item.replace(/,/g, ''));
                    })
                );
                const percentageIncrease =
                    ((sumSelectSection12Data - sumSelectSection2Data) / sumSelectSection2Data) * 100;
                const increasevalue = percentageIncrease.toFixed(2);

                returnData['section3'] = {
                    amount: sumSelectSection12Data,
                    percent: Number(increasevalue),
                    time: selectYear.toString(),
                };
                console.log({ section3: returnData.section3 });
            }

            if (selectSection12Data?.length > 1) {
                const percentageIncrease =
                    ((Number(selectSection12Data[selectSection12Data?.length - 1].replace(/,/g, '')) -
                        Number(selectSection12Data[selectSection12Data?.length - 2].replace(/,/g, ''))) /
                        Number(selectSection12Data[selectSection12Data?.length - 2].replace(/,/g, ''))) *
                    100;
                const increasevalue = percentageIncrease.toFixed(2);

                returnData['section4'] = {
                    amount: Number(selectSection12Data[selectSection12Data?.length - 1].replace(/,/g, '')),
                    percent: Number(increasevalue),
                    time: `${selectSection12Data.length}`,
                };
                console.log({ section4: returnData.section4 });
            }
        }
    }
    console.log({ section1: returnData.section1 });
    return returnData;
};

export default TopChart;
