export const revenueByCategoryList = async ({ data, selectYear }: { data: any; selectYear: string }) => {
    const trList = [];

    if (data && data[selectYear]) {
        for (const key in data[selectYear]) {
            const tdList = [];

            if (data[selectYear].hasOwnProperty(key)) {
                tdList.push(key);
                const value = data[selectYear][key];

                Promise.all(
                    value.map((valueItem: any, index: number) => {
                        tdList.push(valueItem);
                    })
                );
            }
            trList.push(tdList);
        }
    }
    // };

    return trList;
};
export const getScore = async ({ data, selectYear }: { data: any; selectYear: string }) => {
    const serieItems: any = [];
    const scoreList: any = [];
    const typeList: any = [];

    if (data && data[selectYear]) {
        for (const key in data[selectYear]) {
            if (data[selectYear].hasOwnProperty(key)) {
                const value = data[selectYear][key];

                Promise.all(
                    value.map((valueItem: any, index: number) => {
                        if ((index + 1) % 2 === 0) {
                            const resultString = valueItem.replace(/[,%]/g, '');
                            serieItems.push(Number(resultString));
                        } else {
                            typeList.push(valueItem);
                        }
                    })
                );
            }
        }

        const seriesWithIndeces = await serieItems?.map((value: any, index: number) => ({ value, index }));
        seriesWithIndeces.sort((a: any, b: any) => b.value - a.value);
        const indexList = await seriesWithIndeces?.map((item: any) => item.index);

        Promise.all(
            indexList.map((valueItem: any, index: number) => {
                if (index <= 3) {
                    const scoreItem: any = {
                        color: colorList[valueItem],
                        value: serieItems[valueItem],
                        type: typeList[valueItem],
                    };

                    scoreList.push(scoreItem);
                }
            })
        );
    }
    // };

    return scoreList;
};

