import cumulativeRevenueChart, { defaultCumulativeRevenueChart } from './chartOption/cumulativeRevenueChart';
import expenseRatioChart, { defaultExpenseRatioChart } from './chartOption/expenseRatioChart';
import expensesByCategoryChartChart, {
    expensesByCategoryList,
    getScore as getScoreExpensesByCategories,
} from './chartOption/expensesByCategoryChart';
import { getTaxationData, getUserRowData } from '../utill/google';
import optionProfitAndLossChart, { defaultOptionProfitAndLossChart } from './chartOption/optionProfitAndLossChart';
import quarterlyRevenueChart, { defaultQuarterlyRevenueChart } from './chartOption/quarterlyRevenueChart';
import revenueByCategoryChartChart, {
    getScore as getScoreRevenueByCategory,
    revenueByCategoryList,
} from './chartOption/revenueByCategoryChart';
import { useEffect, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

import Content from '../layouts/content';
import Cookies from 'js-cookie';
import QuarterlyRevenueCumulativeRevenue from '../components/dashboard-two/quarterlyRevenueCumulativeRevenue';
import { Row } from '@doar/components';
import RowFour from '../containers/dashboard-one/row-four';
import RowOne from '../containers/dashboard-one/row-one/index';
import RowThree from '../containers/dashboard-one/row-three';
import RowThree2 from '../containers/dashboard-one/row-three2';
import RowTwo2 from '../containers/dashboard-one/row-two2';
import SEO from '../components/seo';
import TopChart from './chartOption/TopChart';
import WelcomeArea from '../containers/dashboard-one/welcome-area';
import moment from 'moment';

const DashboardOne = () => {
    const navigate = useNavigate();
    const [selectYear, setSelectYear] = useState<string>(moment().format('YYYY'));
    const [optionQuarterlyRevenue, setOptionQuarterlyRevenue] = useState<any>(defaultQuarterlyRevenueChart);
    const [optionCumulativeRevenue, setOptionCumulativeRevenue] = useState<any>(defaultCumulativeRevenueChart);
    const [revenueByCategoryData, setRevenueByCategoryData] = useState<any>();
    const [revenueByCategories, setRevenueByCategories] = useState<any>();
    const [scoresRevenueByCategory, setScoresRevenueByCategory] = useState<any>();
    const [scoresExpensesByCategories, setScoresExpensesByCategories] = useState<any>();
    const [expensesByCategoryData, setExpensesByCategoryData] = useState<any>();
    const [expensesByCategories, seteExpensesByCategories] = useState<any>();
    const [optionProfitAndLoss, setOptionProfitAndLoss] = useState<any>(defaultOptionProfitAndLossChart);
    const [optionExpenseRatioChart, setOptionExpenseRatioChart] = useState<any>(defaultExpenseRatioChart);
    const [topChartData, setTopChartData] = useState<any>({
        section1: {
            amount: 0,
            percent: 0,
            time: '',
        },
        section2: {
            amount: 0,
            percent: 0,
            time: '',
        },
        section3: {
            amount: 0,
            percent: 0,
            time: '',
        },
        section4: {
            amount: 0,
            percent: 0,
            time: '',
        },
    });
    const [thList, setThList] = useState<any>([]);
    const [trList, setTrList] = useState<any>([]);
    const [token, setToken] = useState<any>(Cookies.get('CLUST_PARTNERS_TOKEN'));
    useEffect(() => {
        getData({ token });
    }, [token]);
    useEffect(() => {
        const userInfo = Cookies.get('CLUST_PARTNERS_TOKEN');
        if (userInfo) {
            const userData = JSON.parse(userInfo);

            if (userData?.data && moment(userData?.data).isSame(moment().format('YYYY-MM-DD'))) {
                if (userData?.sheetUUID) {
                    void getUserRowData({ token: userData?.sheetUUID });
                }
            } else {
                alert('   .');
                Cookies.remove('CLUST_PARTNERS_TOKEN');
                navigate('/signin');
            }
        }

        // void getUserRowData({ sheetId });
    }, []);

    const dataCopy = useRef<any>(null);

    const [companyNm, setCompanyNm] = useState<string>('');

    const getData = async ({ token }: { token: any }) => {
        console.log(token);

        const tokenItem = JSON.parse(token);
        const retrunData = await getTaxationData({ token: tokenItem?.sheetUUID });

        dataCopy.current = retrunData;
        settingData(retrunData);
    };

    useEffect(() => {
        settingSelectYearData();
    }, [selectYear]);

    const settingData = async (data: any) => {
        if (data?.data?.companyNm) {
            setCompanyNm(data?.data?.companyNm);
        }

        const topData = await TopChart({ data, selectYear: selectYear });
        setTopChartData(topData);

        const optionReturnQuarterlyRevenue = await quarterlyRevenueChart(data?.data?.quarterlyRevenue);
        const optionReturnCumulativeRevenue = await cumulativeRevenueChart(data?.data?.cumulativeRevenue);
        setOptionQuarterlyRevenue(optionReturnQuarterlyRevenue);
        setOptionCumulativeRevenue(optionReturnCumulativeRevenue);
        const revenueByCategory = await revenueByCategoryChartChart({
            data: data?.data?.revenueByCategory,
            selectYear,
        });

        setRevenueByCategoryData(revenueByCategory);
        const revenueByCategoriesData = await revenueByCategoryList({
            data: data?.data?.revenueByCategory,
            selectYear,
        });

        setRevenueByCategories(revenueByCategoriesData);

        const scoresDataRevenueByCategory = await getScoreRevenueByCategory({
            data: data?.data?.revenueByCategory,
            selectYear,
        });
        setScoresRevenueByCategory(scoresDataRevenueByCategory);

        const expensesByCategory = await expensesByCategoryChartChart({
            data: data?.data?.expensesByCategory,
            selectYear,
        });

        setExpensesByCategoryData(expensesByCategory);
        const expensesByCategoriesData = await expensesByCategoryList({
            data: data?.data?.expensesByCategory,
            selectYear,
        });

        seteExpensesByCategories(expensesByCategoriesData);

        const scoresData = await getScoreExpensesByCategories({ data: data?.data?.expensesByCategory, selectYear });
        setScoresExpensesByCategories(scoresData);

        const optionReturnProfitAndLoss = await optionProfitAndLossChart(data?.data?.profitAndLoss);
        const optionReturnExpenseRatioChart = await expenseRatioChart(data?.data?.expenseRatio);
        setOptionProfitAndLoss(optionReturnProfitAndLoss);
        setOptionExpenseRatioChart(optionReturnExpenseRatioChart);
        if (data?.data?.cumulativeRevenue) {
            const list = [];
            for (const key in data?.data?.cumulativeRevenue) {
                list.push(key);
            }

            setThList(list);
            setTrList(data?.data?.cumulativeRevenue);
        }
    };
    const settingSelectYearData = async () => {
        if (dataCopy) {
            const topData = await TopChart({ data: dataCopy.current, selectYear: selectYear });
            console.log({ topData });
            setTopChartData(topData);
            const revenueByCategory = await revenueByCategoryChartChart({
                data: dataCopy.current?.data?.revenueByCategory,
                selectYear,
            });

            setRevenueByCategoryData(revenueByCategory);
            const revenueByCategoriesData = await revenueByCategoryList({
                data: dataCopy.current?.data?.revenueByCategory,
                selectYear,
            });

            setRevenueByCategories(revenueByCategoriesData);

            const scoresDataRevenueByCategory = await getScoreRevenueByCategory({
                data: dataCopy.current?.data?.revenueByCategory,
                selectYear,
            });
            setScoresRevenueByCategory(scoresDataRevenueByCategory);

            const expensesByCategory = await expensesByCategoryChartChart({
                data: dataCopy.current?.data?.expensesByCategory,
                selectYear,
            });

            setExpensesByCategoryData(expensesByCategory);
            const expensesByCategoriesData = await expensesByCategoryList({
                data: dataCopy.current?.data?.expensesByCategory,
                selectYear,
            });

            seteExpensesByCategories(expensesByCategoriesData);

            const scoresData = await getScoreExpensesByCategories({
                data: dataCopy.current?.data?.expensesByCategory,
                selectYear,
            });
            setScoresExpensesByCategories(scoresData);
        }
    };
    return (
        <>
            <SEO />
            <Content>
                <WelcomeArea
                    selectYear={selectYear}
                    setSelectYear={setSelectYear}
                    companyNm={companyNm}
                    topChartData={topChartData}
                />
                <Row gutters={10}>
                    <RowOne topChartData={topChartData} />
                    <QuarterlyRevenueCumulativeRevenue
                        optionQuarterlyRevenue={optionQuarterlyRevenue}
                        optionCumulativeRevenue={optionCumulativeRevenue}
                    />
                    <RowThree
                        revenueByCategoryData={revenueByCategoryData}
                        revenueByCategories={revenueByCategories}
                        scores={scoresRevenueByCategory}
                    />
                    <RowThree2
                        expensesByCategoryData={expensesByCategoryData}
                        expensesByCategories={expensesByCategories}
                        scores={scoresExpensesByCategories}
                    />
                    <RowFour
                        optionProfitAndLoss={optionProfitAndLoss}
                        optionExpenseRatioChart={optionExpenseRatioChart}
                    />
                    <RowTwo2 thList={thList} trList={trList} />
                </Row>
            </Content>
        </>
    );
};

export default DashboardOne;
