import { Button, Modal, ModalBody, ModalClose, ModalFooter, ModalHeader, ModalTitle } from '@doar/components';

import { X } from 'react-feather';

interface IProps {
    show: boolean;
    onClose: () => void;
}

const ModalReport = ({ show, onClose }: IProps) => {
    return (
        <Modal show={show} onClose={onClose}>
            <ModalHeader>
                <ModalTitle>Generate The Report</ModalTitle>
                <ModalClose onClose={onClose}>
                    <X />
                </ModalClose>
            </ModalHeader>
            <ModalBody>
                <p>Do you want to generate the report?</p>
            </ModalBody>
            <ModalFooter>
                <Button>Generate Report</Button>
                <Button color="secondary" onClick={onClose}>
                    Cancel
                </Button>
            </ModalFooter>
        </Modal>
    );
};

export default ModalReport;
